  background-image: linear-gradient(#2A5058, #61a2b1);
}

.button:active {

}

a:link, a:visited {
  color: blue;
  text-decoration: underline;
}

a:hover {
  color: darkblue;
  text-decoration: none;
}

a:active {
  color:navy;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
